.about-me{
    height: 9em;
    width: 9em;
    border-radius: 4.5em;
    text-align: center;
    color: white;
    border: none;
    padding: 20px;
    text-decoration: none;
    font-size: 16px;
    margin: 4px 2px;
}

.about-me:hover{
    -webkit-animation-name: overlays;
            animation-name: overlays;
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes overlays {
    from {opacity: 1}
    to {opacity: 0.6}
}

@keyframes overlays {
    from {opacity: 1}
    to {opacity: 0.6}
}



